#about-section {
  background: #4AB7E7;
  background: linear-gradient(0deg, #CEF3FF 0%, #4AB7E7 100%);
}
#about-section ::-moz-selection {
  color: #ffffff;
  background-color: #063148;
}
#about-section ::selection {
  color: #ffffff;
  background-color: #063148;
}
#about-section ::-moz-selection {
  color: #ffffff;
  background-color: #063148;
}
#about-section p {
  font-size: 1.125rem;
}

#blob {
  background: url(../../../assets/img/ciel_vue1.webp);
  background-size: cover;
  background-position: center center;
  box-shadow: 0 20px 5px 5px rgba(34, 34, 34, 0.067);
  animation: animate 5s ease-in-out infinite;
  transition: all 1s ease-in-out;
}

@keyframes animate {
  0% {
    border-radius: 80% 44% 35% 32%/80% 35% 32% 44%;
    transform: scale(1);
  }
  50% {
    border-radius: 35% 80% 32% 44%/80% 35% 80% 44%;
    transform: scale(0.8);
  }
  100% {
    border-radius: 80% 44% 35% 32%/80% 35% 32% 44%;
    transform: scale(1);
  }
}